* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    /* color: black; */
}

.navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 55px;
    /* background-color: teal; */
    /* color: white; */
    position: fixed;
    width: 100%;
    /* border: 1px solid red; */
    background: none;
    z-index: 5;
}

.nav-links>li>h4:hover {
    background-color: #04b671;
    color: black;
    padding: 5px 10px;
    border-radius: 10px;
    box-shadow: rgba(2, 249, 117, 0.942) 0px 3px 6px, rgba(7, 247, 151, 0.968) 0px 3px 6px;
}


.navbar.active {
    background-image: linear-gradient(to right, #026740, #00FA9A, #047248);
    box-shadow: rgba(75, 237, 249, 0.368) 0px 10px 15px -3px, rgba(4, 45, 252, 0.451) 0px 4px 6px -2px;
    /* background-color: #00FA9A; */
    color: white;
    transition: 0.5s;
}


/* .logo {
    font-size: 30px;
} */

.hamburger {
    color: #00FA9A;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
    position: relative;
    width: 30px;
    bottom: 10px;
}

.nav-links {
    width: 80%;
    height: 40px;
    display: flex;
    /* gap: 20px; */
    justify-content: center;
    list-style: none;
    gap: 40px;
    font-size: 20px;
    /* border: 1px solid blue; */
}

.nav-links>li {
    /* border: 1px solid blueviolet; */
    display: flex;
    justify-content: center;
    align-items: center;
}

.mobile-menu-icon {
    display: none;
}



/* @media screen and (max-width:780px) { */
@media screen and (max-width:480px) {
    /* .navbar.active {
        height: 40px;
        background: none;
        box-shadow: none;
    } */

    .nav-links {
        display: none;
        width: 100%;
        height: 20px;
    }

    .nav-links-mobile {
        position: absolute;
        display: block;
        list-style: none;
        /* background-color: #00FA9A; */
        background-image: linear-gradient(to top, #026740, #00FA9A);
        right: 0;
        /* opacity: 0.3; */
        top: 45px;
        transition: all 0.5s ease-out;
        width: 40%;
        /* color: white; */
        border: 2px solid grey;
        border-bottom-left-radius: 15px;
        border-top-left-radius: 15px;
    }

    .nav-links-mobile>li,
    .themeBtn {
        font-size: 20px;
        border-bottom: 2px solid grey;
        padding: 15px 5px;
        color: black;
    }

    .themeBtn {
        /* margin-bottom: 0px; */
        /* border: none; */
        width: 95%;
        margin: auto;
        border-bottom: none;
    }


    .mobile-menu-icon {
        display: block;
        position: absolute;
        font-size: 30px;
        color: white;
        background: none;
        outline: none;
        top: 15px;
        right: 25px;
        transition: '1s, transform 6s',
    }

    .Landing-con-child-2 {
        /* border: 1px solid blue; */
        position: absolute;
        left: 0px;
        top: 50px;
    }

    .Header-intro>h2>div {
        font-size: 40px;
    }

    .Typewriter__wrapper {
        /* width: 100%; */
        /* border: 1px solid red; */
        font-size: 23px;
    }

    .Landing-con-child-2>.animation-container {
        /* border: 1px solid nred; */
        position: absolute;
        top: 100px;
        left: 50px;
        width: 100%;
    }

    .Landing-con-child-2 {
        width: 80%;
        /* border: 1px solid rgb(0, 255, 17); */
    }

    .animation-container .ring:nth-child(2) {
        position: relative;
        left: -100px;
    }

}


@media screen and (max-width:820px) {
    .nav-links>li {
        font-size: 14px;
    }

    .Landing {
        /* border: 1px solid blanchedalmond; */
    }

    .Landing-con-child-2>.animation-container {
        /* border: 1px solid nred; */
        position: absolute;
        right: 0px;
    }

    .animation-container .ring:nth-child(2) {
        position: relative;
        left: -100px;
    }

    .Header>.Header-intro>h2>div {
        font-size: 50px;
    }

    .Typewriter__wrapper {
        font-size: 30px;
    }

}